$activeBtn: #fed45c;
$fontRob: 'Roboto', sans-serif;
$fontPop: 'Poppins', sans-serif;
$fontOs: 'Oswald', sans-serif;

@mixin center{
     justify-content: center;
     align-items: center;
}



html{
     font-size: 10px;          
}

header{
     background-image: url('../../img/headerBg.png');
     background-repeat: no-repeat;
     background-size: cover;
     height: 57.5rem;
     position: relative;
}
.nav{
     height: 6.25rem;
     display: flex;
     align-items: center;
     padding: 0 3.75rem;
     justify-content: space-between
}
.nav__logo{
     color: #ffffff;
     font-family: $fontOs;
     font-weight: 400;
     font-size: 1.75rem;

     &:hover{
          color: #ffffff;
          text-decoration: none
     }
}

.nav__menu{

     display: flex;
     align-items: center;
     justify-content: flex-end
}
.nav__menu ul{
     display: flex;
     margin: 0 0;
     justify-content: space-between;
     min-width: 35rem;
     list-style-type: none;
     color: #ffffff;
     font-size: 1rem;
     font-weight: 700;
     font-family: $fontRob;
     text-transform: uppercase;

}
.nav__menu ul li{
     cursor: pointer;
}
.nav__menu span{
     width: 40px;
     height: 40px;
     border: 2px solid #fff;
     border-radius: 50%;
     display: none;
     @include center();
     color: #fff;
     font-size: 1.3rem;
     cursor: pointer;
     margin-left: 1.25rem;
     
}
.headTitle{
     display: flex;
     @include center();
     margin-top: 11.25rem;
     flex-direction: column;

}
.headTitle__h1{
     color: #ffffff;
     font-size: 2.8rem;
     font-family: $fontRob;
     font-weight: 400;
     letter-spacing: -0.9px;
     text-align: center
}
.headTitle__text{
     color: #ffffff;
     font-family: "Open Sans";
     font-size: 20px;
     font-weight: 400;
     margin-top: 1rem;
     text-align: center
}
.headTitle__btn{
     width: 17.3rem;
     height: 3.7rem;
     box-shadow: 0 0 30px rgba(254, 212, 92, 0.5);
     background-color: $activeBtn;
     display: flex;
     @include center();
     border-radius: 1.8rem;
     color: #222222;
     font-family: $fontPop;
     font-size: 1rem;
     text-transform: uppercase;
     letter-spacing: 0.42px;
     margin-top: 2rem;
     
     &:hover{
          color:#222222;
          text-decoration: none;

     }
}
.tech__{
     display: flex;
     justify-content: space-around;
     margin-top: 20.3rem;
}
.teсh__items{
     height: 10.9rem;
     width: 11.25rem;
     background-color: #fff;
     box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
     display: flex;
     flex-direction: column;
     align-items: center;
     justify-content: center;
}
.teсh__items p{
     font-family: "Open Sans";
     font-size: 0.9rem;
     font-weight: 400;
     text-transform: uppercase;
     letter-spacing: 0.65px;
}
.grow{
     margin-top: 12.5rem;
}
.grow__context__subtext{
     color: #777777;
     font-family: $fontPop;
     font-weight: 400;
     font-size: 0.9rem;
     text-transform: uppercase;
     margin-bottom: 0.9rem;
}
.grow__content__title{
     color: #222222;
     font-family: $fontRob;
     font-size: 2.1rem;
     font-weight: 300;
     margin-bottom: 1.5rem;
}
.grow__content__text1{
     color: #777777;
     font-size: 1.1rem;
     font-family: $fontRob;
     font-weight: 300;
     margin-bottom: 1.38rem;
}
.grow__content__text2{
     color: #777777;
     font-family: "Open Sans";
     font-size: 0.9rem;
     font-weight: 400;
     margin-bottom: 2.2rem;
}
.grow__content__btn{
     width: 11.9rem;
     height: 3.1rem;
     background-color: $activeBtn;
     box-shadow: 0 0 30px rgba(254, 212, 92, 0.5);
     border-radius: 1.8rem;
     display: flex;
     @include center();
     cursor: pointer;
     font-size: 0.75rem;
     font-family: $fontPop;
     font-weight: 400;
     text-transform: uppercase;
     color: #222222;
}
.grow__img{
     display: flex;
     justify-content: center
}
.grow__img img{
     height: 20rem;
     width: 30rem;
     
}
.message{
     display: flex;
     flex-direction: column;
     align-items: center;
     margin-top: 6.25rem;
}
.message__title{
     color: #555555;
     font-family: $fontRob;
     font-size: 0.9rem;
     text-transform: uppercase;
     font-weight: 400;
     margin-top: 1rem;
}
.message__text{
     color: #777777;
     font-size: 1.25rem;
     font-family: $fontRob;
     font-weight: 300;   
     max-width: 778px;
     text-align: center;
     margin-top: 1rem;
}
.message__subtext{
     color: #222222;
     font-family: $fontRob;
     font-weight: 400;
     font-size: 0.9rem;
     text-transform: uppercase;
}
.section{
     background-color: #fcfcfc;
     width: 100%;
     margin-top: 7rem;
}
.about{
     display: flex;
}


.about__items{
     padding: 7rem 2rem;
     display: flex;
     flex-direction: column;
     align-items: center;
}
.about__items__title{
     color: #222222;
     font-family: $fontRob;
     font-weight: 300;
     font-size: 1.8rem;
     margin-top: 2.5rem;
}
.about__items__text{
     text-align: center;
     color: #777777;
     font-family: "Open Sans";
     font-weight: 400;
     font-size: 1rem;
}
.laptop{
     background-image: url('../../img/laptopBg.png');
     background-repeat: no-repeat;
     background-size: cover;
     min-width: 100%;
     min-height: 41.9rem;
     display: flex;
}